.lt-header,
.lt-navbar {
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;

	.nav, 
	.dropdown-menu {
		font-size: $font-size-sm;
		line-height: $line-height-sm;
	}

	.dropdown .nav-link::before {
		position: absolute;
		top: calc(100% - 0.5rem);
		left: calc(50% - 0.5rem);
		content: '';
		height: 0;
		width: 0;
		border-bottom: 0.5rem solid $gray-12;
		border-left: 0.5rem solid transparent;
		border-right: 0.5rem solid transparent;
		z-index: 1001;
		display: none;
	}

	.dropdown .nav-link.show::before {
		display: block;
	}

	.dropdown-dark .nav-link::before {
		border-bottom-color: $gray-2;
	}

	.dropdown-menu {
		background-color: $gray-12;
		border: 0;
		border-radius: 0;
		margin-top: 0;
		padding: 0.75rem 1rem;
		box-shadow: 0 2px 4px 0 rgba($black, 0.15);

		.dropdown-item {
			display: block;
			font-weight: 400;
			line-height: 1.5;
			white-space: nowrap;
			padding: 0.25rem 0;
			color: $gray-4;

			&:hover,
			&:focus {
				background-color: transparent;
				color: $link-color;
			}
		}

		@include media-breakpoint-up(lg) {
			padding: 1.5rem;
		}
	}

	.dropdown-menu-dark {
		background-color: $gray-2;

		.dropdown-item {
			color: $white;

			&:hover,
			&:focus {
				color: $link-inverse-hover-color;
			}
		}
	}

	.dropdown-toggle {

		&::after {
			display: inline-block;
			width: 0.375rem;
			height: 0.375rem;
			margin: 0 0.25rem;
			vertical-align: 0.125em;
			content: '';
			border-top: 0;
			border-left: 0;
			border-right: 1px solid currentColor;
			border-bottom: 1px solid currentColor;
			transform-origin: bottom right;
			transform: rotate(45deg);
		}

		&.show::after {
			vertical-align: 0.5em;
			transform: rotate(225deg);
		}
	}

	.nav-link {
		white-space: nowrap;
	}

	.nav:not(.nav-dark-neutral) .nav-link  {
		color: $nav-link-color;
		&:hover,
		&:focus {
			color: $nav-link-hover-color;
		}
	}

	.nav-neutral {
		.nav-link:not(.disabled) {
			color: $body-color;
			&:hover,
			&:focus {
				color: $link-color;
			}
		}
		.nav-link.disabled {
			color: $gray-9;
		}
	}

	.nav-dark-neutral {
		background-color: $gray-4;
		.nav-link:not(.disabled,.link-inverse) {
			color: $white;
			&:hover,
			&:focus {
				color: $link-inverse-color;
			}
		}
		.nav-link.disabled {
			color: $gray-8;
		}
	}

	.navbar-brand {
		padding-right: 0.4375rem;
		margin-right: 0;
	}
}

.lt-header {
	background-color: $gray-4;
	border-bottom: $border-width solid $gray-11;

	.ico-person-circle-outline {
		vertical-align: middle;
		margin-right: 0.5rem;
		position: relative;
		top: -0.0625rem;
	}

	@include media-breakpoint-down(lg) {
		.ico-person-circle-outline {
			margin-right: 0;
		}

		.nav .nav-item .nav-link {
			color: $gray-4;
			margin: 0;
			padding: 0.5rem 1rem 0.5rem 1.25rem;
			white-space: normal;
		}
	}

	.link-inverse {
		color: $link-inverse-color;

		&:hover,
		&:focus {
			color: $link-inverse-hover-color;
		}
	}
}

.lt-header-dropdown-col {
	@include media-breakpoint-up(lg) {
		width: 24rem;
		column-count: 2;

		li {
			break-inside: avoid;
		}
	}
}

.lt-header-mobile-bar {
	.col:last-child {
		padding-right: 0.5rem;
	}

	.nav-link {
		color: $gray-5 !important;
		padding: 0.375rem 0.5rem !important;
	}

	.dropdown .nav-link::before {
		top: 100%;
	}

	.dropdown-menu {
		width: 100vw;
		margin-top: 0.4375rem;
		margin-right: -0.5rem;
	}
}
	

.lt-header-nav {
	top: 3.5rem;
	width: 100%;
	border-right: 0;

	@include media-breakpoint-up(lg) {
		position: static;
		visibility: visible !important;
		flex-direction: row;
		flex-wrap: wrap;
		justify-content: space-between;
		background-color: $gray-4;
		transform: none;
	}
}

.lt-header-primary {
	@include media-breakpoint-down(lg) {
		.navbar-brand {
			display: none;
		}

		.nav {
			display: block;
			font-size: $font-size-base;
			line-height: $line-height-base;	
		}

		.nav-link {
			background-color: $gray-12;

			&.active {
				background-color: $gray-11;
    			border-right: 3px solid var(--lt-nav-highlight);
    			padding-right: calc(1rem - 3px);
			}
		}
	}

	@include media-breakpoint-up(lg) {
		position: relative;
		display: flex;
		align-items: center;
		flex: 1 0 100%;
		order: 3;
		padding: 0 1rem;

		.dropdown-toggle {
			&::after {
				color: $gray-5;
			}

			&:hover,
			&:focus {
				&::after {
					color: currentColor;
				}
			}
		}

		.nav-link {
			padding: 1.125rem 0 0.9375rem;
			margin: 0 0.5625rem;
			border-bottom: 0.1875rem solid transparent;

			&.active {
				border-bottom-color: var(--lt-nav-highlight);
			}
		}
	}
}

.lt-header-utility,
.lt-header-global {
	.dropdown-menu {
		padding: 0.75rem 1rem;
	}
}

.lt-header-utility {
	@include media-breakpoint-down(lg) {
		.lt-header-utility-major {
			display: none;
		}
	}

	@include media-breakpoint-up(lg) {
		order: 2;
		display: flex;
		justify-content: flex-end;
		padding-right: 0.5rem;

		.nav-link {
			padding: 0.5rem 0;
			margin: 0 0.5rem;
		}
	}
}

.lt-header-global {
	@include media-breakpoint-down(lg) {
		.nav-link {			
			&.active {
				border-left: 3px solid var(--lt-nav-highlight);
    			padding-left: calc(1.25rem - 3px);
			}
		}
	}

	@include media-breakpoint-up(lg) {
		order: 1;

		.nav-link {
			color: $white !important;
			border-right: 1px solid $gray-5;
			padding: $nav-link-padding-y $container-padding-x;

			&:hover,
			&:focus {
				background-color: $gray-3;
			}

			&.active {
				color: $gray-4 !important;
				background-color: $gray-11;
				border-right: 0;
				border-top: 0.25rem solid var(--lt-nav-highlight);
				padding-top: $nav-link-padding-y - 0.25rem;
				&:not(:first-child) {
					margin-left: -1px;
				}

				&:hover,
				&:focus {
					background-color: $gray-10;
				}
			}

			&.dropdown-toggle {
				&.show {
					background-color: $gray-2;
				}

				&::after {
					position: relative;
					left: -0.1875em;
				}
				
			}
		}

		.dropdown-divider {
			margin: 0.75rem -1rem 1.25rem;
		}
	}
}


.lt-header-mobile-bar {
	font-size: 0.75rem;
	height: 3.5rem;
	border-top: 4px solid var(--lt-nav-highlight);
	.nav-link {
		padding: 0.5rem;
	}
}

// secondary navbar
.lt-navbar {
	position: relative;
	display: flex;
	font-size: 0.8125rem;
	background-color: $gray-11;
	padding: 0 1rem;
	align-items: baseline;
	flex-wrap: nowrap;

	.navbar-brand {
		color: $gray-4;
		font-size: 0.875rem;
		font-weight: 700;
	}

	.nav, 
	.dropdown-menu {
		font-size: $font-size-sm;
		line-height: $line-height-sm;
	}

	.nav {
		flex-wrap: nowrap;
	}

	.nav-link {
		border-bottom: 0.1875rem solid transparent;
		padding: 0.75rem 0 0.5625rem;
		margin: 0 0.5625rem;

		&.active {
			border-bottom-color: var(--lt-nav-highlight);
		}
	}

	@include media-breakpoint-up(lg) {
		font-size: 0.875rem;
		align-items: center;

		.navbar-brand {
			font-size: 1.25rem;
		}
	}
}

.navbar-toggler {
	padding: 0.5rem 1rem;
	border: 0;
	line-height: 1;

	&:focus {
		box-shadow: none;
	}
}

.navbar-toggler-icon {
	position: relative;
	display: inline-block;
	vertical-align: middle;
	width: 20px;
	height: 16px;
	padding-top: 7px;
	padding-bottom: 8px;
	background-color: $body-color;
	background-clip: content-box;
	transition: background-color 0.2s linear;

	&::before,
	&::after {
		content: '';
		position: absolute;
		left: 0;
		width: 100%;
		height: 1px;
		background-color: $body-color;
		transition: transform 0.2s cubic-bezier(0.23, 1, 0.32, 1);
	}

	&::before {
		top: 0;
	}

	&::after {
		bottom: 1px;
	}

	.navbar-toggler-open & {
		background-color: transparent;

		&::before {
			top: 7px;
			transform: rotate(-45deg);
		}

		&::after {
			bottom: 8px;
			transform: rotate(45deg);
		}
	}
}

[data-lt-navbar-overflow] {
	&:not(.lt-navbar-overflow) {
		overflow: hidden; // remove scrollbars until initalized
	}	
	animation-duration: 0.001s;
	animation-name: navbarInserted; // detect if an overflow has been inserted via javascript animation detection
}

@keyframes navbarInserted {  
	from { opacity: 0.99; }
	to { opacity: 1; }  
}