:root {
  --lt-nav-highlight: #aaa;
}

html {
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
  height: 100%;
}
body > * {
  flex-shrink: 0;
}

main {
  flex-grow: 1;
}

.lt-header,
.lt-navbar {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.lt-header .nav,
.lt-header .dropdown-menu,
.lt-navbar .nav,
.lt-navbar .dropdown-menu {
  font-size: 0.875rem;
  line-height: 1.71486;
}
.lt-header .dropdown .nav-link::before,
.lt-navbar .dropdown .nav-link::before {
  position: absolute;
  top: calc(100% - 0.5rem);
  left: calc(50% - 0.5rem);
  content: "";
  height: 0;
  width: 0;
  border-bottom: 0.5rem solid #f5f5f5;
  border-left: 0.5rem solid transparent;
  border-right: 0.5rem solid transparent;
  z-index: 1001;
  display: none;
}
.lt-header .dropdown .nav-link.show::before,
.lt-navbar .dropdown .nav-link.show::before {
  display: block;
}
.lt-header .dropdown-dark .nav-link::before,
.lt-navbar .dropdown-dark .nav-link::before {
  border-bottom-color: #222;
}
.lt-header .dropdown-menu,
.lt-navbar .dropdown-menu {
  background-color: #f5f5f5;
  border: 0;
  border-radius: 0;
  margin-top: 0;
  padding: 0.75rem 1rem;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.15);
}
.lt-header .dropdown-menu .dropdown-item,
.lt-navbar .dropdown-menu .dropdown-item {
  display: block;
  font-weight: 400;
  line-height: 1.5;
  white-space: nowrap;
  padding: 0.25rem 0;
  color: #444;
}
.lt-header .dropdown-menu .dropdown-item:hover, .lt-header .dropdown-menu .dropdown-item:focus,
.lt-navbar .dropdown-menu .dropdown-item:hover,
.lt-navbar .dropdown-menu .dropdown-item:focus {
  background-color: transparent;
  color: #0078aa;
}
@media (min-width: 992px) {
  .lt-header .dropdown-menu,
.lt-navbar .dropdown-menu {
    padding: 1.5rem;
  }
}
.lt-header .dropdown-menu-dark,
.lt-navbar .dropdown-menu-dark {
  background-color: #222;
}
.lt-header .dropdown-menu-dark .dropdown-item,
.lt-navbar .dropdown-menu-dark .dropdown-item {
  color: #fff;
}
.lt-header .dropdown-menu-dark .dropdown-item:hover, .lt-header .dropdown-menu-dark .dropdown-item:focus,
.lt-navbar .dropdown-menu-dark .dropdown-item:hover,
.lt-navbar .dropdown-menu-dark .dropdown-item:focus {
  color: #5ee8ff;
}
.lt-header .dropdown-toggle::after,
.lt-navbar .dropdown-toggle::after {
  display: inline-block;
  width: 0.375rem;
  height: 0.375rem;
  margin: 0 0.25rem;
  vertical-align: 0.125em;
  content: "";
  border-top: 0;
  border-left: 0;
  border-right: 1px solid currentColor;
  border-bottom: 1px solid currentColor;
  transform-origin: bottom right;
  transform: rotate(45deg);
}
.lt-header .dropdown-toggle.show::after,
.lt-navbar .dropdown-toggle.show::after {
  vertical-align: 0.5em;
  transform: rotate(225deg);
}
.lt-header .nav-link,
.lt-navbar .nav-link {
  white-space: nowrap;
}
.lt-header .nav:not(.nav-dark-neutral) .nav-link,
.lt-navbar .nav:not(.nav-dark-neutral) .nav-link {
  color: #0078aa;
}
.lt-header .nav:not(.nav-dark-neutral) .nav-link:hover, .lt-header .nav:not(.nav-dark-neutral) .nav-link:focus,
.lt-navbar .nav:not(.nav-dark-neutral) .nav-link:hover,
.lt-navbar .nav:not(.nav-dark-neutral) .nav-link:focus {
  color: #006088;
}
.lt-header .nav-neutral .nav-link:not(.disabled),
.lt-navbar .nav-neutral .nav-link:not(.disabled) {
  color: #444;
}
.lt-header .nav-neutral .nav-link:not(.disabled):hover, .lt-header .nav-neutral .nav-link:not(.disabled):focus,
.lt-navbar .nav-neutral .nav-link:not(.disabled):hover,
.lt-navbar .nav-neutral .nav-link:not(.disabled):focus {
  color: #0078aa;
}
.lt-header .nav-neutral .nav-link.disabled,
.lt-navbar .nav-neutral .nav-link.disabled {
  color: #ccc;
}
.lt-header .nav-dark-neutral,
.lt-navbar .nav-dark-neutral {
  background-color: #444;
}
.lt-header .nav-dark-neutral .nav-link:not(.disabled, .link-inverse),
.lt-navbar .nav-dark-neutral .nav-link:not(.disabled, .link-inverse) {
  color: #fff;
}
.lt-header .nav-dark-neutral .nav-link:not(.disabled, .link-inverse):hover, .lt-header .nav-dark-neutral .nav-link:not(.disabled, .link-inverse):focus,
.lt-navbar .nav-dark-neutral .nav-link:not(.disabled, .link-inverse):hover,
.lt-navbar .nav-dark-neutral .nav-link:not(.disabled, .link-inverse):focus {
  color: #00cbed;
}
.lt-header .nav-dark-neutral .nav-link.disabled,
.lt-navbar .nav-dark-neutral .nav-link.disabled {
  color: #aaa;
}
.lt-header .navbar-brand,
.lt-navbar .navbar-brand {
  padding-right: 0.4375rem;
  margin-right: 0;
}

.lt-header {
  background-color: #444;
  border-bottom: 1px solid #e8e8e8;
}
.lt-header .ico-person-circle-outline {
  vertical-align: middle;
  margin-right: 0.5rem;
  position: relative;
  top: -0.0625rem;
}
@media (max-width: 991.98px) {
  .lt-header .ico-person-circle-outline {
    margin-right: 0;
  }
  .lt-header .nav .nav-item .nav-link {
    color: #444;
    margin: 0;
    padding: 0.5rem 1rem 0.5rem 1.25rem;
    white-space: normal;
  }
}
.lt-header .link-inverse {
  color: #00cbed;
}
.lt-header .link-inverse:hover, .lt-header .link-inverse:focus {
  color: #5ee8ff;
}

@media (min-width: 992px) {
  .lt-header-dropdown-col {
    width: 24rem;
    column-count: 2;
  }
  .lt-header-dropdown-col li {
    break-inside: avoid;
  }
}

.lt-header-mobile-bar .col:last-child {
  padding-right: 0.5rem;
}
.lt-header-mobile-bar .nav-link {
  color: #707070 !important;
  padding: 0.375rem 0.5rem !important;
}
.lt-header-mobile-bar .dropdown .nav-link::before {
  top: 100%;
}
.lt-header-mobile-bar .dropdown-menu {
  width: 100vw;
  margin-top: 0.4375rem;
  margin-right: -0.5rem;
}

.lt-header-nav {
  top: 3.5rem;
  width: 100%;
  border-right: 0;
}
@media (min-width: 992px) {
  .lt-header-nav {
    position: static;
    visibility: visible !important;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    background-color: #444;
    transform: none;
  }
}

@media (max-width: 991.98px) {
  .lt-header-primary .navbar-brand {
    display: none;
  }
  .lt-header-primary .nav {
    display: block;
    font-size: 1rem;
    line-height: 1.5;
  }
  .lt-header-primary .nav-link {
    background-color: #f5f5f5;
  }
  .lt-header-primary .nav-link.active {
    background-color: #e8e8e8;
    border-right: 3px solid var(--lt-nav-highlight);
    padding-right: calc(1rem - 3px);
  }
}
@media (min-width: 992px) {
  .lt-header-primary {
    position: relative;
    display: flex;
    align-items: center;
    flex: 1 0 100%;
    order: 3;
    padding: 0 1rem;
  }
  .lt-header-primary .dropdown-toggle::after {
    color: #707070;
  }
  .lt-header-primary .dropdown-toggle:hover::after, .lt-header-primary .dropdown-toggle:focus::after {
    color: currentColor;
  }
  .lt-header-primary .nav-link {
    padding: 1.125rem 0 0.9375rem;
    margin: 0 0.5625rem;
    border-bottom: 0.1875rem solid transparent;
  }
  .lt-header-primary .nav-link.active {
    border-bottom-color: var(--lt-nav-highlight);
  }
}

.lt-header-utility .dropdown-menu,
.lt-header-global .dropdown-menu {
  padding: 0.75rem 1rem;
}

@media (max-width: 991.98px) {
  .lt-header-utility .lt-header-utility-major {
    display: none;
  }
}
@media (min-width: 992px) {
  .lt-header-utility {
    order: 2;
    display: flex;
    justify-content: flex-end;
    padding-right: 0.5rem;
  }
  .lt-header-utility .nav-link {
    padding: 0.5rem 0;
    margin: 0 0.5rem;
  }
}

@media (max-width: 991.98px) {
  .lt-header-global .nav-link.active {
    border-left: 3px solid var(--lt-nav-highlight);
    padding-left: calc(1.25rem - 3px);
  }
}
@media (min-width: 992px) {
  .lt-header-global {
    order: 1;
  }
  .lt-header-global .nav-link {
    color: #fff !important;
    border-right: 1px solid #707070;
    padding: 0.5rem 1rem;
  }
  .lt-header-global .nav-link:hover, .lt-header-global .nav-link:focus {
    background-color: #333;
  }
  .lt-header-global .nav-link.active {
    color: #444 !important;
    background-color: #e8e8e8;
    border-right: 0;
    border-top: 0.25rem solid var(--lt-nav-highlight);
    padding-top: 0.25rem;
  }
  .lt-header-global .nav-link.active:not(:first-child) {
    margin-left: -1px;
  }
  .lt-header-global .nav-link.active:hover, .lt-header-global .nav-link.active:focus {
    background-color: #ddd;
  }
  .lt-header-global .nav-link.dropdown-toggle.show {
    background-color: #222;
  }
  .lt-header-global .nav-link.dropdown-toggle::after {
    position: relative;
    left: -0.1875em;
  }
  .lt-header-global .dropdown-divider {
    margin: 0.75rem -1rem 1.25rem;
  }
}

.lt-header-mobile-bar {
  font-size: 0.75rem;
  height: 3.5rem;
  border-top: 4px solid var(--lt-nav-highlight);
}
.lt-header-mobile-bar .nav-link {
  padding: 0.5rem;
}

.lt-navbar {
  position: relative;
  display: flex;
  font-size: 0.8125rem;
  background-color: #e8e8e8;
  padding: 0 1rem;
  align-items: baseline;
  flex-wrap: nowrap;
}
.lt-navbar .navbar-brand {
  color: #444;
  font-size: 0.875rem;
  font-weight: 700;
}
.lt-navbar .nav,
.lt-navbar .dropdown-menu {
  font-size: 0.875rem;
  line-height: 1.71486;
}
.lt-navbar .nav {
  flex-wrap: nowrap;
}
.lt-navbar .nav-link {
  border-bottom: 0.1875rem solid transparent;
  padding: 0.75rem 0 0.5625rem;
  margin: 0 0.5625rem;
}
.lt-navbar .nav-link.active {
  border-bottom-color: var(--lt-nav-highlight);
}
@media (min-width: 992px) {
  .lt-navbar {
    font-size: 0.875rem;
    align-items: center;
  }
  .lt-navbar .navbar-brand {
    font-size: 1.25rem;
  }
}

.navbar-toggler {
  padding: 0.5rem 1rem;
  border: 0;
  line-height: 1;
}
.navbar-toggler:focus {
  box-shadow: none;
}

.navbar-toggler-icon {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  width: 20px;
  height: 16px;
  padding-top: 7px;
  padding-bottom: 8px;
  background-color: #444;
  background-clip: content-box;
  transition: background-color 0.2s linear;
}
.navbar-toggler-icon::before, .navbar-toggler-icon::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: #444;
  transition: transform 0.2s cubic-bezier(0.23, 1, 0.32, 1);
}
.navbar-toggler-icon::before {
  top: 0;
}
.navbar-toggler-icon::after {
  bottom: 1px;
}
.navbar-toggler-open .navbar-toggler-icon {
  background-color: transparent;
}
.navbar-toggler-open .navbar-toggler-icon::before {
  top: 7px;
  transform: rotate(-45deg);
}
.navbar-toggler-open .navbar-toggler-icon::after {
  bottom: 8px;
  transform: rotate(45deg);
}

[data-lt-navbar-overflow] {
  animation-duration: 0.001s;
  animation-name: navbarInserted;
}
[data-lt-navbar-overflow]:not(.lt-navbar-overflow) {
  overflow: hidden;
}

@keyframes navbarInserted {
  from {
    opacity: 0.99;
  }
  to {
    opacity: 1;
  }
}
.lt-footer {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #333;
  padding: 2rem 1rem 6rem;
}

.lt-footer-nav {
  text-transform: uppercase;
  font-size: 0.6875rem;
  line-height: 1rem;
  letter-spacing: 0.05em;
  font-weight: 700;
}

.lt-footer-legal.nav-dark .nav-link,
.lt-footer-copyright {
  font-size: 0.75rem;
  line-height: 1rem;
  color: #aaa;
}

.lt-footer-legal {
  margin-bottom: 0.25rem;
}
.lt-footer-legal .nav-dark .nav-link:hover, .lt-footer-legal .nav-dark .nav-link:focus {
  color: #fff;
}